html {
  font-size: 62.5%;
}

body {
  color: $text-color;
  padding-top: 6rem;
  @media(min-width: 768px) {
    padding: 9rem 3rem 0 3rem;
  }

  &.mobile-nav-active {
    overflow-y: hidden;
  }
}

h1 {
  font-size: 2rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: .2rem;
  line-height: 1.5em;
  @media(min-width: 768px) {
    font-size: 2.4rem;
  }
}

h2 {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 1em;
  text-transform: uppercase;
  letter-spacing: .3rem;
  @media(min-width: 768px) {
    font-size: 2.4rem;
  }
  @media(min-width: 1920px) {
    font-size: 3rem;
  }
}

.module-header {
  &:after {
    content: '';
    display: block;
    margin: 2rem auto 0 auto;
    width: 0;
    border-top: 1px solid $gold;
    transition: width 500ms ease 300ms;
  }

  &.active:after {
    width: 15rem;
  }
}

h3.component-header {
  margin: 0;
  color: $gold;
  text-transform: uppercase;
  text-align: center;

  &:after {
    content: '';
    display: block;
    width: 4rem;
    height: .5rem;
    margin: 3rem auto 6rem auto;
    background: $text-color;
  }
}

p {
  font-size: 1.4rem;
  @media(min-width: 768px) {
    font-size: 1.6rem;
  }
}

.btn {
  line-height: 1em;
  display: inline-block;
  text-transform: uppercase;
  background: transparent;
  position: relative;
  overflow: hidden;
  letter-spacing: .1rem;
  font-size: 1.2rem;
  font-weight: 700;
  margin: 2em 0;
  @include transition(color 300ms ease-out);

  .right-arrow {
    fill: $gold;
    margin-left: .5rem;
  }
}

&.no-border {
  border: none;
  padding: 0;
  overflow: visible;
  letter-spacing: .4rem;

  &:hover {
    color: $gold;
    background: none;
  }

  &:after {
    display: none;
  }

  span {
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -.5rem;
      width: 100%;
      display: block;
      border-bottom: .1rem solid $gold;
      opacity: 0;
      @include transition(opacity 300ms ease);
    }
    &:hover:after {
      opacity: 1;
    }
  }
}

.cover-link {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

form {
  margin: 0 auto;
  box-sizing: content-box;

  .fields,
  ul.gform_fields {
    @include flexbox();
    @include flex-wrap(wrap);
  }

  .field,
  .gfield {
    padding: .5rem;
    @include flex(0 0 100%);
  }

  .field.half,
  .gfield.half {
    @include flex(0 0 50%);
  }

  label {
    display: block;
    text-align: left;
  }

  input[type=text],
  input[type=email],
  input[type=password] {
    width: 100%;
    height: 5rem;
    padding: 0 2rem;
    border: none;
    background: #F9F9F9;
    font-family: $font-family-sans;
    font-size: 1.4rem;
    @media(min-width: 992px) {
      font-size: 1.6rem;
    }

    &:focus {
      outline: .1rem solid #ddd;
    }
  }

  textarea {
    width: 100%;
    height: 36rem;
    padding: 1.5rem;
    border: none;
    background: #f9f9f9;
    font-family: $font-family-sans;

    &:focus {
      outline: .1rem solid #ddd;
    }
  }

  .submit,
  .gform_footer,
  .login-submit {
    text-align: center;
    margin-top: 3rem;

    .btn,
    .gform_button,
    input[type=submit] {
      border: 1px solid $text-color;
      width: 25rem;
      font-size: 1.2rem;
      font-weight: 700;
      display: inline-block;
      text-transform: uppercase;
      padding: 2rem;
      background: transparent;
      position: relative;
      overflow: hidden;
      @include transition(all 200ms ease-out);

      &:hover {
        background-color: $text-color;
        color: #fff;
      }
    }
  }

  & .gform_body {
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    label {
      display: none;
    }
  }
}
